                  <v-col class=" text-center content white--text">
                    <app-button
                      :custom-class="'buttonPrimary theme-button theme--light subtitle-1 pa-1'"
                      v-if="vipProgress.has_pending_claim && currentLvlCode == reward.level"
                      :action="claimVipProgress"
                      :title="$t(`label.claim`)"
                    ></app-button>
